import Container from 'react-bootstrap/Container';
import ImageHis1 from './Styles/historiaCafe.png';
import ImageHis2 from './Styles/historiaCup.png';
import './Carrusel.css'

const Historia = () =>{
    return (
        <>
        <Container>
            
      
        <br /><br /><br />
        <h1 className='tituloHis'>HISTORIA DEL CUPCAKE</h1><br />
        <div className='letraHis'>El nombre de cupcake significa literalmente pastel-taza, del origen del nombre hay dos versiones, la primera versin es que ese nombre se le di en el siglo XIX porque se horneaban en tazas de barro individuales (taza es cup en ingls), la segunda versin de la procedencia del nombre es que se debe a la manera de calcular los ingredientes empleados en su elaboracin que se miden por tazas.</div>
        <br />
        <div className='letraHis'>El origen de estos pasteles se remonta hasta el ao de 1796 cuando Amelia Simms public una receta en su libro de cocina American Cookery, pero no fue hasta el siglo XXI cuando los cupcakes se pusieron de moda en Estados Unidos donde se abrieron pasteleras que tenan como nico protagonista este divertido pastel.</div>
        <br /><br /><br />
        <img  src={ImageHis2} alt="cup" id='imagenHis'/>
        <br /><br /><br /><br /><br /><br />
        <h2 className='tituloHis'>HISTORIA DEL CAF</h2><br />
        <div className='letra2His'>La leyenda del pastor </div>  <br /><div className='letraHis'>Segn una leyenda, fue un joven pastor yemen llamado Kaldi quien descubri la planta del caf. Kaldi observ un comportamiento extrao en su rebao, que brincada y saltaba enrgicamente, tras ingerir los frutos rojos de un arbusto hasta entonces desconocido. Sorprendido por este hecho el pastor arranc varios frutos y se los mostr a un superior religioso de un convento cercano. El religioso prob a hervir las bayas y beber la infusin resultante, pero era demasiado amarga y la desech. Al tirar aquellos frutos al fuego observ cmo al tostarse desprendan un agradable aroma. De nuevo prob a preparar una infusin con las bayas ya tostadas, descubriendo entonces lo que hoy conocemos como caf.</div>
        
        <br /><div className='letra2His'>Las montaas de Etiopa</div>
        <br /><div className='letraHis'>Los historiadores sitan las primeras plantas de caf en las regiones montaosas de Etiopa, antes del siglo IX. Desde all pasaron al vecino Yemen, situado al Sur de la Pennsula Arbica, desde donde fueron llevadas hasta Ceiln y la India. El caf fue introducido en Europa cuando varios ejemplares de cafetos procedentes de Java fueron llevados al Jardn Botnico de Amsterdam. Desde entonces el cultivo del caf se extendi por el continente, llegando incluso a las colonias que los pases europeos tenan en Amrica. El consumo de caf comenz a generalizarse en Europa a partir del siglo XVIII.</div>
        <br /><br /><br />
        <img src={ImageHis1} alt="cafe" id='imagenHis'  />
        <br /><br /><br />
        </Container>
        </>

    )
}

export default Historia;